* {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
}

.App {
  position: relative;
  width: 100%;
  height: 100%;
}

.App.connector-mode .Arrow circle,
.App.connector-mode .Arrow .connectionDot {
	pointer-events: none;
}

.App .Arrow circle,
.App .Arrow .connectionDot {
	cursor: pointer;
	pointer-events: all;
}

.App .Sticky .snapping-point {
	pointer-events: none;
}

.App.connector-mode .Sticky .snapping-point {
	pointer-events: all;
}

.App.connector-mode .Sticky:hover .snapping-point ~ .auto-affordance {
  visibility: visible;
}

.App.connector-mode .Sticky .snapping-point:hover ~ .auto-affordance {
  visibility: hidden;
}

#settings input {
  margin-right: 10px;
}

#settings hr {
  margin-top: 10px;
  margin-bottom: 10px;
}

#settings .labelInput {
  display: inline-block;
  width: 150px;
  border: solid 1px black;
  background-color: white;
  padding: 2px;
}
